<template>
   <div id="canvas">
      <form :name="name">
         <app-container
            :row="row"
            :styles="styles"
            :value="{
               name: name,
               type: 'AppContainer',
               childs: row,
               options: {
                  styles: styles
               }
            }"
            @push="onPush($event)"
            @change="setValue($event)"
         ></app-container>
      </form>
      <app-modal-box
         v-if="modalBox.show"
         @close="modalBox.show = false"
      >
         <form>
            <components
               v-for="(value, key) of modalBox.obj"
               :key="key"
               :is="key"
               :value="value"
               @change="setValue({
                  pos: modalBox.pos,
                  val: $event,
                  key: key,
                  type: modalBox.type
               })"
               @addOpt="addOpt({
                  pos: modalBox.pos,
                  key: key,
                  type: modalBox.type
               })"
               @changeOpt="changeOpt({
                  pos: modalBox.pos,
                  key: key,
                  type: modalBox.type,
                  val: $event.val,
                  index: $event.index
               })"
            ></components>
            <el-input></el-input>
         </form>
      </app-modal-box>
   </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import AppContainer from './Container.vue'
import AppModalBox from './ModalBox.vue'
// styles
import Background from './styles/Background.vue' 
import Flex from './styles/Flex.vue' 
import Size from './styles/Size.vue'
import Margin from './styles/Margin.vue'
import Padding from './styles/Padding.vue'
import Border from './styles/Border.vue'
import Font from './styles/Font.vue'
import Overflow from './styles/Overflow.vue'
// value
import Value from './values/Value.vue'
import MyLabel from './values/Label.vue'
import MyOption from './values/Option.vue'

export default {
   components: {
      AppContainer,
      AppModalBox,
      Background,
      Flex,
      Size,
      Margin,
      Padding,
      Border,
      Font,
      Overflow,
      Value,
      MyLabel,
      MyOption
   },
   data: ()=>({
      modalBox: {
         show: false,
         obj: {},
         pos: [],
         type: ''
      }
   }),
   computed: {
      ...mapGetters({
         name: 'getRootName',
         styles: 'getRootStyle',
         row: 'getRootValue',
         get: 'getItem'
      })
   },
   methods: {
      ...mapMutations({
         addItem: 'addItem',
         delItem: 'delItem',
         setValue: 'setItemProps',
         addOpt: 'addOpt',
         changeOpt: 'changeOpt'
      }),
      onPush (e) {
         let name = e.name
         let ev = {
            pos: e.pos,
            val: e.val
         }
         if(name === 'add')
            this.addItem(ev)
         else if (name === 'editStyle') {
            this.modalBox.pos = e.pos
            this.modalBox.obj = this.get(e.pos).options.styles
            this.modalBox.show = true
            this.modalBox.type = "styles"
         } else if (name === 'editValue') {
            this.modalBox.pos = e.pos
            this.modalBox.obj = this.get(e.pos).options.values
            this.modalBox.show = true
            this.modalBox.type = "values"
         } else if (name === 'del')
            this.delItem(ev)
      }
   }
}
</script>

<style lang="scss" scoped>

   #canvas {
      padding: 20px;
      flex: 1 1 auto;
      background-color: #b3b3b3;

      & form {
         width: 100%;
         height: 100%;
      }
   }

</style>